<template>
  <div id="userLogin">
    <div style="text-align:center">
     <mt-header fixed title="证件信息录入" style="background-color: #eaeaea; color:#000000; border:1px;"></mt-header>
    </div>
    <div @click="actionSheet">
       <mt-field label="证件类型" v-model="userType" readonly ></mt-field>
    </div>
    <hr style="background-color: #3366ff">
    <mt-field label="证件号码" placeholder="证件号码" v-model="userLoginCode"></mt-field>
    <hr style="background-color: #3366ff">
    <mt-field label="手机号码" placeholder="手机号码" v-model="userPhones"></mt-field>
    <hr style="background-color: #3366ff">

    <br/>
    <div style="text-align:center"> 
      <mt-button type="primary" style="width: 80%" @click="UserLogin">确 定</mt-button>
    </div>
    <mt-actionsheet
      :actions="selectData"
      v-model="sheetVisible">
    </mt-actionsheet>

  </div>
</template>

<script>
    export default {
      name: "UserLogin",
      data () {
        return {
          userType : "居民身份证",
          userTypeCode : '120001',
          userLoginCode:"",
          userPhones:"",
          // action sheet 默认不显示，为false。操作sheetVisible可以控制显示与隐藏
          sheetVisible: false,
          selectData:[{
            name: '居民身份证',
            type:'120001',
            method:this.setUserType
          },{
            name: '护照',
            type:'120002',
            method:this.setUserType
          },{
            name: '军人证',
            type:'120003',
            method:this.setUserType
          },{
            name: '其它（个人）',
            type:'120009',
            method:this.setUserType
          },{
            name: '外国人永久居留身份证',
            type:'1200553',
            method:this.setUserType
          }]
        }
      },
      methods : {
        actionSheet () {
          this.sheetVisible = true;
        },
        setUserType(data){
          this.userType=data.name
          this.userTypeCode=data.type
          console.log(data)
        },
        UserLogin(){
          let userCode = this.$store.getters.user.userCode;
          if(userCode){
            this.$store.commit('LocalUser',{userCode:userCode,userTypeCode:this.userTypeCode,userLoginCode:this.userLoginCode,userPhones:this.userPhones})
            console.log("用户标识："+userCode+" ;证件类型：" + this.userTypeCode + "_" + this.userType + " ;用户证件号码：" + this.userLoginCode + " ;用户手机号：" +  this.userPhones + " ；验证码:" + this.codeMsg)
            this.$http.post("/checkLoginData",{userCode:userCode,userTypeCode:this.userTypeCode,userLoginCode:this.userLoginCode,userPhones:this.userPhones})
              .then(
                (response)=>{
                  let type = response.data.status;
                  console.log(type)
                  if(type){
                    console.log( response.data.msg)
                    if(type=='identifying'){
                      this.$router.push('/identifying');
                    } else {
                      alert(response.data.msg)
                    }
                  }
                },
                (error)=>{
                  console.log(error);
                }
              )
          } else {
            alert("数据异常，请重新登录")
          }
        }
      }
    }
</script>
<style scoped>  
</style>


